import {lazyComponent} from '@/utils/util'

export default[
    {
        path: '/projectList',
        name: 'projectList',
        component: lazyComponent('project/list',true),
        meta: {
            title: '项目管理',
            name:'项目管理'
        }
    },
    {
        path: '/projectDetail/:projectId',
        name: 'projectDetail',
        component: lazyComponent('project/detail',true),
        meta: {
            title: '项目详情',
            name:'项目详情'
        }
    },
]


