import {lazyComponent} from '@/utils/util'

export default[
    {
        path: '/visitList',
        name: 'visitList',
        component: lazyComponent('visit/List'),
        meta: {
            title: '拜访记录',
            name:'拜访记录'
        }
    },
    {
        path: '/visitNew',
        name: 'visitNew',
        component: lazyComponent('visit/New'),
        meta: {
            title: '新建拜访记录',
            name:'新建拜访记录'
        }
    },
    {
        path: '/visitEdit/:visitId',
        name: 'visitEdit',
        component: lazyComponent('visit/New'),
        meta: {
            title: '拜访记录修改',
            name:'拜访记录修改'
        }
    },
    {
        path: '/visitDetail/:visitId',
        name: 'visitDetail',
        component: lazyComponent('visit/Detail'),
        meta: {
            title: '拜访记录详情',
            name:'拜访记录详情'
        }
    },
]


