import Vue from 'vue'
// import Moment from 'moment'
import {fmt} from '@/utils/util'

// 时间过滤器
Vue.filter('dateFormat', function (input, type) {
	return fmt.date(input,type)
});

// 状态过滤器
Vue.filter('supportType', function (input) {
	let text
	switch (input) {
		case 1:
			text = '资金'
			break
		case 2:
			text = '技术支持'
			break
		case 3:
			text = '协访'
			break
		default:
			text = '其他'
	}
	return text
});
Vue.filter('planType', function (input) {
	let text
	switch (input) {
		case 1:
			text = '日计划'
			break
		case 2:
			text = '周计划'
			break
		case 3:
			text = '月计划'
			break
		default:
			text = '数据错误'
	}
	return text
});
Vue.filter('planState', function (input) {
	let text
	switch (input) {
		case 1:
			text = '草稿'
			break
		case 2:
			text = '已发布'
			break
		case 3:
			text = '删除'
			break
		default:
			text = '数据错误'
	}
	return text
});
Vue.filter('customerLevel', function (input) {
	let text
	switch (input) {
		case 1:
			text = 'VIP'
			break
		case 2:
			text = '重要客户'
			break
		case 2:
			text = '重要客户'
			break
		default:
			text = '数据错误'
	}
	return text
});



