import {lazyComponent} from '@/utils/util'
// 新增与编辑使用共同的组建，通过判断是否有queryId区分 新建和编辑
export default[
    {
        path: '/serviceList',
        name: 'serviceList',
        component: lazyComponent('service/list',true),
        meta: {
            title: '服务管理',
            name:'服务管理'
        }
    },
    {
        path: '/serviceNew',
        name: 'serviceNew',
        component: lazyComponent('service/new',true),
        meta: {
            title: '新建服务',
            name:'新建服务' 
        }
    },
    {
        path: '/serviceEdit',
        name: 'serviceEdit',
        component: lazyComponent('service/new',true),
        meta: {
            title: '服务修改',
            name:'服务修改'
        }
    },
    {
        path: '/serviceDetail/:serviceId',
        name: 'serviceDetail',
        component: lazyComponent('service/detail',true),
        meta: {
            title: '服务详情',
            name:'服务详情'
        }
    },
]


