import axios from '@/utils/fetch'
const modelUrl = 'customer'

/**
 * 
 * @param {*}{客户名称/联系人,行业类型Id,客户级别,负责人,创建时间,分页数,分页大小}
 */
export const getCustomerList=({name,industry_type_id,customer_level,director_name,create_time,page_num,page_size})=>{
    return axios({
        url:`${modelUrl}/paging`,
        params:{name,industry_type_id,customer_level,director_name,create_time,page_num,page_size}
    })
}
/**
 * 新增客户
 * @param {*} {客户名称,社会信用代码,客户级别,行业类型Id,行业子类型Id,省Id,详细地址,公司网址,营业额（元）,主营业务,营业执照URL,客户概述,附件列表,联系人列表,}
 */
export const postNewCustomer=({customer_name,social_credit_code,customer_level,industry_type_id,industry_subtype_id,province_id,address,website,turnover,primary_business,business_licence_img,customer_summary,attaches,linkmen})=>{
    return  axios({
        url:`${modelUrl}`,
        data:{customer_name,social_credit_code,customer_level,industry_type_id,industry_subtype_id,province_id,address,website,turnover,primary_business,business_licence_img,customer_summary,attaches,linkmen},
        method:'POST'
    })
}
/**
 * 修改客户
 */
export const putEditCustomer=({id,customer_name,social_credit_code,customer_level,industry_type_id,industry_subtype_id,province_id,address,website,turnover,primary_business,business_licence_img,customer_summary,attaches,linkmen})=>{
    return  axios({
        url:`${modelUrl}`,
        data:{id,customer_name,social_credit_code,customer_level,industry_type_id,industry_subtype_id,province_id,address,website,turnover,primary_business,business_licence_img,customer_summary,attaches,linkmen},
        method:'PUT'
    })
}
/**
 * 根据姓名查询客户信息
 * @param {*} name 
 */
export const getCustomerByName=(customer_name)=>{
    return axios({
        url:`${modelUrl}/search`,
        params:{customer_name}
    })
}
/**
 * 查询客户联系人信息
 * @param {*} 姓名,联系方式,职务 
 */
export const getCustomerLinkMan = ({linkman_name,linkman_contact,linkman_post})=>{
    return axios({
        url:`customer_linkman/search`,
        params:{linkman_name,linkman_contact,linkman_post}
    })
}
/**
 * 
 * @param {*} 根据Id获取客户信息
 */
export const getCustomerById=(id)=>{
    return axios({
        url:`${modelUrl}/${id}`,
    })
}
/**
 * 根据Id获取客户详情
 */
export const getCustomerDetailById=(id)=>{
    return axios({
        url:`${modelUrl}/detail/${id}`,
    })
}
/**
 * 根据Id删除客户
 */
export const delCustomerById=(id)=>{
    return axios({
        url:`${modelUrl}/${id}`,
        method:'DELETE'
    })
}
export const checkIsDelCustomer=(id)=>{
    return axios({
        url:`customer_linkman/delete/check/${id}`,
    })
}



