import {lazyComponent} from '@/utils/util'

export default[
    {
        path: '/planList',
        name: 'planList',
        component: lazyComponent('plan/List'),
        meta: {
            title: '计划管理',
            name:'计划管理'
        }
    },
    {
        path: '/planNew',
        name: 'planNew',
        component: lazyComponent('plan/New'),
        meta: {
            title: '新建计划',
            name:'新建计划'
        }
    },
    {
        path: '/planEdit/:planId',
        name: 'planEdit',
        component: lazyComponent('plan/New'),
        meta: {
            title: '计划修改',
            name:'计划修改'
        }
    },
    {
        path: '/planDetail/:planId',
        name: 'planDetail',
        component: lazyComponent('plan/Detail'),
        meta: {
            title: '计划详情',
            name:'计划详情'
        }
    },
]


