import {lazyComponent} from '@/utils/util'
// 新增和编辑使用公共组件，编辑需传入query.id，组件据此区分新增还是编辑

export default[
    {
        path: '/knowledgeList',
        name: 'knowledgeList',
        component: lazyComponent('knowledge/list',true),
        meta: {
            title: '知识库管理',
            name:'知识库管理'
        }
    },
    {
        path: '/knowledgeNew',
        name: 'knowledgeNew',
        component: lazyComponent('knowledge/new',true),
        meta: {
            title: '新建知识库',
            name:'新建知识库'
        }
    },
    {
        path: '/knowledgeEdit',
        name: 'knowledgeEdit',
        component: lazyComponent('knowledge/new',true),
        meta: {
            title: '知识库修改',
            name:'知识库修改'
        }
    },
    {
        path: '/knowledgeDetail/:knowledgeId',
        name: 'knowledgeDetail',
        component: lazyComponent('knowledge/detail',true),
        meta: {
            title: '知识库详情',
            name:'知识库详情'
        }
    },
]


